* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
.wrap {
  position: relative;
  clear: both;
  width: 340px;
  height: 1470px;
  margin: 0 auto;
  background: #ddd;
  overflow: hidden;
}


.container {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 340px;
  height: 500px;
}
main {
  position:absolute;
  left: 0px;
  top: 0px;
  width: 340px;
}
  .sidebar {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 200px;
    height: 50vh;
    z-index: 9999;
    background: rgb(30, 30, 30. 0.5);
    color: #fff;
    transition: all 0.5s;
  }
    .top_section {
      display: flex;
      align-items: center;
      padding: 0;
      width: 30px;
      height: 30px;
    }
      .logo {
        font-size: 10px;
      }
      .bars {
        display: flex;
        font-size: 14px;
        margin-left: 50px;
      }
.link {
  display: flex;
  color: #fff;
  padding: 10px 10px;
  gap: 15px;
  transition: all 0.5s;
}     
.link:hover {
  background:#fff;
  color: #000;
  transition: all 0.5s;
} 
.active {
  background: rgba(255, 255, 255, 0.5);
  color: #000;
}
.icon, .link_text {
  font-size: 12px;
}






